/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Athiti&display=swap");
@import url("https://fonts.googleapis.com/css?family=Poiret+One&display=swap");
@import url("https://fonts.googleapis.com/css?family=Barriecito&display=swap");
@import url("https://fonts.googleapis.com/css?family=Lilita+One&display=swap");
@import url(../../fonts/fonts.css);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  border: 12px solid #DCDCDC;
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh; }

main {
  display: flex;
  justify-content: center;
  height: 400px; }

footer {
  text-align: end;
  padding: 50px 30px;
  font-family: 'Athiti'; }
  footer a {
    text-decoration: none;
    font-weight: bold; }
    footer a:visited {
      color: #000000; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.title-section {
  display: flex;
  flex-direction: column;
  margin: 0 auto; }

.title {
  font-family: 'Poiret One';
  font-size: 2.25rem;
  text-transform: uppercase;
  margin: 50px 0;
  cursor: pointer; }

.title-animated {
  text-align: center;
  color: black;
  font-family: 'Athiti';
  text-transform: uppercase;
  font-size: 18px;
  width: 260px;
  align-self: center;
  padding-bottom: 20px;
  font-weight: bold; }

.curly-arrow {
  background-image: url(../../images/arrow-pointer.svg);
  background-size: contain;
  background-repeat: no-repeat;
  transform: rotate(140deg);
  height: 60px;
  width: 50px;
  position: absolute;
  left: -8px;
  bottom: 115px;
  animation: blink-animation 1s steps(5, start) infinite;
  -webkit-animation: blink-animation 1s steps(5, start) infinite; }

@keyframes blink-animation {
  to {
    visibility: hidden; } }

@-webkit-keyframes blink-animation {
  to {
    visibility: hidden; } }

header {
  margin: 30px auto;
  position: relative;
  text-align: center; }

nav {
  width: 95%;
  margin: 0 auto; }

.site-menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  font-family: 'Athiti';
  text-transform: uppercase;
  font-size: 16px; }
  .site-menu li a {
    color: #000000;
    text-decoration: none;
    transition: border-bottom 0.3s ease-in; }
    .site-menu li a:visited {
      color: #000000; }
    .site-menu li a:hover {
      border-bottom: 4px solid #000; }

.clock-menu {
  grid-column: 1 / 2;
  grid-row: 1 / 2; }

.weather-menu {
  grid-column: 3 / 4;
  grid-row: 1 / 2; }

.unit-menu {
  grid-column: 3 / 4;
  grid-row: 3 / 4; }

.todo-menu {
  grid-column: 1 / 2;
  grid-row: 3 / 4; }

.calculator-menu {
  grid-column: 2 / 3;
  grid-row: 2 / 3; }

html, body {
  font-size: 16px; }

.about {
  font-family: 'Athiti';
  line-height: 2;
  padding: 50px; }

.about-list {
  font-size: 14px;
  padding-top: 20px;
  font-weight: bold; }

.made-with {
  padding-top: 20px;
  font-style: italic; }

.clock-container {
  display: none;
  padding-top: 80px; }

.clock {
  font-size: 3rem;
  font-family: 'Digital-7';
  border: 3px solid #2C3C50;
  padding: 30px;
  background-color: #DCDCDC;
  color: #2C3C50;
  border-radius: 5px; }

.location, .weather {
  display: none; }

.location {
  flex-direction: column;
  align-items: center; }
  .location .icon {
    width: 250px;
    padding: 20px 0; }

.location-timezone {
  font-family: 'Athiti';
  font-size: 1.25rem;
  padding: 10px 0; }

.weather {
  font-family: 'Athiti';
  text-align: center; }

.degree-section {
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: bold; }

.weather-description {
  padding: 20px 0; }

.calculator {
  display: none; }
  .calculator span {
    display: flex; }

.temp-description {
  font-family: 'Athiti';
  font-size: 14px;
  text-transform: uppercase;
  padding: 5px 2px; }

#input-temp, #input-conv {
  border: 0.5px solid #DCDCDC;
  border-radius: 5px;
  padding: 5px;
  color: #5d5d5d;
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

.unit-convertor {
  display: none; }

.unit input[type="number"] {
  -moz-appearance: textfield; }

.unit input::-webkit-outer-spin-button,
.unit input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

.conv p {
  font-family: 'Athiti';
  font-size: 14px;
  text-transform: uppercase;
  padding: 5px 2px; }

.todo {
  display: none;
  padding-top: 80px; }

input[type="text"] {
  outline: 0;
  border: none; }

button {
  outline: 0;
  border: none; }

.tasksBoard {
  display: none; }

ul {
  list-style: none; }

a {
  cursor: pointer; }

.delete {
  cursor: pointer; }
